'use strict';

/*
* When adding modules to the namespace, ensure that they are added in alphabetical order according to module name.
*/

// MODULES //

import setReadOnly from '@stdlib/utils/define-read-only-property';


// MAIN //

/**
* Top-level namespace.
*
* @namespace ns
*/
var ns = {};

/**
* @name arcsine
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/random/strided/arcsine}
*/
import arcsine from './../../strided/arcsine';
setReadOnly( ns, 'arcsine', arcsine );

/**
* @name beta
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/random/strided/beta}
*/
import beta from './../../strided/beta';
setReadOnly( ns, 'beta', beta );

/**
* @name discreteUniform
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/random/strided/discrete-uniform}
*/
import discreteUniform from './../../strided/discrete-uniform';
setReadOnly( ns, 'discreteUniform', discreteUniform );

/**
* @name exponential
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/random/strided/exponential}
*/
import exponential from './../../strided/exponential';
setReadOnly( ns, 'exponential', exponential );

/**
* @name lognormal
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/random/strided/lognormal}
*/
import lognormal from './../../strided/lognormal';
setReadOnly( ns, 'lognormal', lognormal );

/**
* @name normal
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/random/strided/normal}
*/
import normal from './../../strided/normal';
setReadOnly( ns, 'normal', normal );

/**
* @name randu
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/random/strided/randu}
*/
import randu from './../../strided/randu';
setReadOnly( ns, 'randu', randu );

/**
* @name uniform
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/random/strided/uniform}
*/
import uniform from './../../strided/uniform';
setReadOnly( ns, 'uniform', uniform );


// EXPORTS //

export default ns;
